"use client";

import {
  Task,
  TaskItem,
  TaskItemFile,
  TaskTrigger,
  TaskContent,
} from "@/components/ai-elements/task";
import { Button } from "@/components/ui/button";

import {
  SiReact,
  SiTypescript,
  SiJavascript,
  SiCss,
  SiHtml5,
  SiJson,
  SiMarkdown,
} from "@icons-pack/react-simple-icons";

type AiFile = {
  path: string;
  contents: string;
};

interface ChatBoxProps {
  object: { files: AiFile[]; TaskItem: string } | undefined;

}
const Content = ({ object }: ChatBoxProps) => {
  const getLanguageFromFileName = (fileName: string) => {
    const extension = fileName.split(".").pop();
    switch (extension) {
      case "ts":
        return "typescript";
      case "tsx":
        return "react";

      case "js":
        return "javascript";
      case "jsx":
        return "react";
      case "json":
        return "json";
      case "html":
        return "html";
      case "css":
        return "css"; // Added CSS
      case "md":
        return "markdown"; // Added Markdown
      default:
        return "plaintext";
    }
  };

  function renderTaskItem(file:AiFile, index: number) {
    
    const iconInfo = iconMap[getLanguageFromFileName(file.path) as keyof typeof iconMap];
    if (iconInfo) {
      const IconComponent = iconInfo.component;
      return (
        <span className="inline-flex items-center gap-1" key={index}>
          <TaskItemFile>
            <IconComponent
              color={iconInfo.color}
              className="size-4"
            />
            <span>{file.path}</span>
          </TaskItemFile>
        </span>
      );
    }
  }
  const iconMap = {
    react: { component: SiReact, color: "#149ECA" },
    typescript: { component: SiTypescript, color: "#3178C6" },
    javascript: { component: SiJavascript, color: "#F7DF1E" },
    css: { component: SiCss, color: "#1572B6" },
    html: { component: SiHtml5, color: "#E34F26" },
    json: { component: SiJson, color: "#000000" },
    markdown: { component: SiMarkdown, color: "#000000" },
  };

  return (
    <div className="p-4">
      {object && object.files && object.files.length > 0 && (
        <div className="mt-4">
          <h2 className="text-xl font-bold mb-4">
            Generated Files ({object.files.length})
          </h2>
          <div className="space-y-3 max-h-[600px] overflow-y-auto">
            {object.files.map((file, index) => (
              <Task key={index} defaultOpen={index === 0}>
                <TaskContent>
                  <TaskItem key={index}>
                    {renderTaskItem(file,index)}
                  </TaskItem>
                </TaskContent>
              </Task>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Content;
